<template>
  <van-form @submit="onSubmit">
    <h2>Profile</h2>
    <van-cell-group>
      <van-field
        v-model="formData.fullname"
        name="FullName"
        label="FullName"
        placeholder="FullName"
        :rules="[{ required: true, message: 'FullName is required' }]"
      />
      <van-field
        v-model="formData.address"
        name="Address"
        label="Address"
        placeholder="Address"
        :rules="[{ required: true, message: 'Address is required' }]"
      />
      <van-field
        v-model="formData.phone"
        name="Phone"
        label="Phone"
        placeholder="Phone"
        :rules="[{ required: true, message: 'Phone is required' }]"
      />
      <van-field label="Age">
        <template #input>
          <van-stepper v-model="formData.age" theme="round" button-size="22" />
        </template>
      </van-field>
    </van-cell-group>

    <div style="margin: 16px;">
      <van-button round block type="info" native-type="submit">
        Submit
      </van-button>
    </div>
  </van-form>
</template>
<script>
export default {
  name: "ProfileForm",
  data() {
    return {
      formData: {
        age: 18,
        fullname: "",
        phone: "",
        address: ""
      }
    };
  },
  methods: {
    onSubmit() {
      console.log("submit");
      this.$emit("submit", this.formData);
    }
  }
};
</script>
